.skillsDetails{
    background-color: #FFFFFF;
    border: 1px solid black;
    width: 85%;
    margin: 3% auto 1%;
    box-shadow: 0px 5px 15px 5px #bbbbbb;
}

*{
    font-size: 16px;
}
h1{
    padding: 0;
    margin: 0;
    font-family: 'Bona Nova', serif;
    font-size: 25px;
    letter-spacing: 2px;
    padding: 2.5% 0 0 2.5%;

}

.skillsBanner h1{
    margin:0;
    font-size: 45px;
    font-family: 'Montserrat', sans-serif;

}

.frontEnd, .backend{
    display: grid;
    grid-template-columns: 13% 13% 13% 13% 13% 13% 13%;
    height: 30vh;
    background-color: #FFFFFF;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.backend{
    grid-template-columns: 13% 13% 13% 13%;
}

.frontEnd>div, .backend>div{
    width: 55%;
    height: 17vh;
    margin: 0px auto;
    box-shadow: 0px 5px 10px 1px lightgray;
    padding:15px;
}
.frontEnd>div:hover, .backend>div:hover{
    transform: scale(1.1); 
    box-shadow: 3px 0px 15px 2px #c5c5c5;
    transition: transform 330ms ease-in-out;
}
.frontEnd p, .backend p{
    text-align:center;
    padding: 0;
    margin: 0;
    padding-top: 4%;
}

.frontEnd>div img , .backend>div img{
  width: 90%;
  height: 12vh;
  margin: 0px auto;
}

.down{
    padding: 0;
    margin: 0;
    text-align: center;
    font-size: 50px;
}

@media only screen and (min-width: 1200px) {

    .skillsBanner > img{
        width: 47%;
        height: 50%;
        margin-left: 20%;
    }
     .frontEnd>div img , .backend>div img{
        width: 80%;
        height: 10vh;
        margin: 0px auto;
      }
    .github-calendar{
        width: 80%;
        margin: 30px auto;
    }
    .stacistics-container{
        width: 90%;
        margin: auto;
        justify-content: center;
        margin: 1rem auto;
    }

    .stacistics-box{
        display:flex;
        width: 95%;
        margin: auto;
    }

    .individualStat > div:nth-child(1) {
        margin: auto;
        font-size: 12px;
        font-weight: 600;
        height: 80px;
        width: 80px;
        border: 5px solid #506aff;
        border-radius: 65px;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .statisticsTitle > h1 {
        font-weight: 500;
        margin-top: 100px;
        position: relative;
        width: fit-content;
    }
}


