.main-stat{
    display: flex;
    width: 85%;
    margin: auto;
    flex-direction: column;
    text-align: center;
    border: 2px solid ;
}
.stacistics-container{
    width: 90%;
    margin: auto;
    justify-content: center;
    margin: 2rem auto;
}
.stacistics-box{
    display:flex;
    justify-content: space-around;
}
.statisticsTitle > h1 {
    font-weight: 700;
    margin-top: 100px;
    position: relative;
    width: fit-content;
  }
  
  .statisticsTitle > h1::after {
    background-color: #506aff;
    content: " ";
    height: 20%;
    position: absolute;
    left: -5px;
    top: 55%;
    width: calc(100% + 10px);
    z-index: -1;
  }
  
  .individualStat {
    width: 140px;
    text-align: center;
  }
  
  .individualStat > div:nth-child(1) {
    margin: auto;
    font-size: 28px;
    font-weight: 600;
    height: 130px;
    width: 130px;
    border: 10px solid #506aff;
    border-radius: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
  }


